.header{
    display: flex;
    align-items: center;
    flex-direction: row;
    background-color: #001C30;
}
.logo>a{
    text-decoration: none;
    font-family: sans-serif;
    font-weight: 800;
    color: #DAFFFB;
}
h1, h2{
    margin: 0;
}


.menu{
    margin-left: 40px;
    display: flex;
    flex-direction: row;
}
.link{
    text-decoration: none;
}
H4{
    
    font-size: 1.2rem;
    color: rgb(197, 197, 197);
    margin-right: 15px;
}
H4:hover{
    color: white;
}

@media (max-width: 470px) {
    .header{
        flex-direction: column;
    }
    .menu{
        margin-left: 10px;
    }
    h2{
        margin-top: 15px;
        font-size: 20px;
    }
    h4{
        font-size: 15px;
    }
}